@import url(https://fonts.googleapis.com/css?family=Raleway:200);
body {
  margin: 0;
  font-family: "Raleway", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body,
html,
#root,
.App {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
  display: flex;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.blurred {
  -webkit-filter: blur(10px);
          filter: blur(10px);
}

.header-bar-container{height:60px;display:flex;justify-content:center;align-items:center;margin-bottom:10px;border-bottom:1px solid lightgrey;font-weight:bold}.header-bar-container img{position:absolute;left:15px;cursor:pointer}

.dashboard-body-container{margin-top:30px;display:flex;justify-content:space-evenly;flex-wrap:wrap}

.current-weather-block{border:1px solid lightgrey;border-radius:10px;width:300px;height:300px}.current-weather-block .current-weather-body{display:flex;flex-direction:column;height:auto}.current-weather-block .current-weather-body .icon{margin-top:10px;margin-bottom:20px}.current-weather-block .current-weather-body .temperature,.current-weather-block .current-weather-body .description{font-size:22pt;margin-bottom:10px}

.block-header-container{border-bottom:1px solid lightgrey;margin-bottom:10px;width:100%;height:50px;display:flex;justify-content:center;align-items:center;font-weight:bold}

.today-training-block{border:1px solid lightgrey;border-radius:10px;width:1000px;height:300px}.today-training-block .today-training-body{display:flex;flex-direction:row;justify-content:space-evenly}.today-training-block .today-training-body .today-training-am,.today-training-block .today-training-body .today-training-pm{width:50%}.today-training-block .today-training-body .today-training-am{border-right:1px solid lightgrey}.today-training-block .today-training-body .icon{margin-top:20px;margin-bottom:20px}.today-training-block .today-training-body .exercise{font-size:22pt;margin-bottom:10px}

.todo-list-container{border:1px solid lightgrey;border-radius:10px;width:1350px;height:350px;margin-top:30px}.todo-list-container .todo-list-body{display:flex;flex-direction:column;height:auto}.todo-list-container .todo-list-body .form-control:disabled{background-color:#fff}.todo-list-container .todo-list-body .todo-input{width:100%;outline:none;border:none}.todo-list-container .todo-list-body .input-box{border-bottom:1px solid lightgray}.todo-list-container .todo-list-body .add-todo-button{float:right}.todo-list-container .todo-list-body .task{cursor:pointer}.todo-list-container .todo-list-body .completed{text-decoration:line-through}.todo-list-container .todo-list-body .icon{margin-top:10px;margin-bottom:20px}.todo-list-container .todo-list-body .temperature,.todo-list-container .todo-list-body .description{font-size:22pt;margin-bottom:10px}

.hamburger-menu-open,.hamburger-menu-closed{border-right:1px solid lightgray;height:100%;width:300px;background-color:#fff;z-index:99}.hamburger-menu-open{position:absolute;left:0;top:0}.hamburger-menu-open img{position:absolute;left:15px;top:10px;cursor:pointer}.hamburger-menu-closed{display:none}

